// =============================================================================
// SMF DEBUG CLIENT
// =============================================================================
// Client helper for fetching SMF facilitator debug data

import type { FacilitatorPathMetricsTimeframe } from '@/infra/x402scan/types';

// =============================================================================
// TYPES
// =============================================================================

export interface FacilitatorMetrics {
  successRate: number;
  errorRate: number;
  p95LatencyMs?: number;
  invocations: number;
  dataAgeHours: number;
}

export interface FacilitatorDebugInfo {
  id: string;
  name: string;
  baseUrl: string;
  networks: string[];
  score: number;
  rank: number;
  selected: boolean;
  x402scanScore?: number;
  confidence?: 'high' | 'medium' | 'low' | 'none';
  shortReason?: string;
  metrics?: FacilitatorMetrics;
  reasons?: string[];
}

export interface SmfFacilitatorsDebugResponse {
  ok: true;
  network: string | null;
  timeframe: FacilitatorPathMetricsTimeframe;
  usedFallback: boolean;
  recommended: string | null;
  recommendationReason: string;
  facilitators: FacilitatorDebugInfo[];
  timestamp: string;
}

export interface SmfFacilitatorsErrorResponse {
  ok: false;
  error: string;
}

export type SmfFacilitatorsResponse = SmfFacilitatorsDebugResponse | SmfFacilitatorsErrorResponse;

export interface FetchSmfFacilitatorsParams {
  network?: string;
  timeframe?: FacilitatorPathMetricsTimeframe;
}

// =============================================================================
// NETWORK OPTIONS
// =============================================================================

export const NETWORK_OPTIONS = [
  { value: '', label: 'All Networks' },
  { value: 'eip155:8453', label: 'Base Mainnet (eip155:8453)' },
  { value: 'eip155:84532', label: 'Base Sepolia (eip155:84532)' },
  { value: 'eip155:1', label: 'Ethereum Mainnet (eip155:1)' },
  { value: 'eip155:137', label: 'Polygon (eip155:137)' },
  { value: 'eip155:56', label: 'BSC (eip155:56)' },
  { value: 'solana:mainnet', label: 'Solana Mainnet' },
  { value: 'solana:101', label: 'Solana Mainnet (101)' },
  { value: 'solana:devnet', label: 'Solana Devnet' },
  { value: 'xdc:50', label: 'XDC Mainnet' },
] as const;

export const TIMEFRAME_OPTIONS = [
  { value: '1d', label: '1 Day' },
  { value: '7d', label: '7 Days' },
  { value: '30d', label: '30 Days' },
] as const;

// =============================================================================
// CLIENT
// =============================================================================

/**
 * Fetch SMF facilitators debug data from the API
 */
export async function fetchSmfFacilitatorsDebug(
  params: FetchSmfFacilitatorsParams = {}
): Promise<SmfFacilitatorsResponse> {
  try {
    const searchParams = new URLSearchParams();
    
    if (params.network) {
      searchParams.set('network', params.network);
    }
    if (params.timeframe) {
      searchParams.set('timeframe', params.timeframe);
    }

    const queryString = searchParams.toString();
    const url = `/api/debug/smf-facilitators${queryString ? `?${queryString}` : ''}`;

    const response = await fetch(url, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      cache: 'no-store',
    });

    const data = await response.json();

    if (!response.ok) {
      return {
        ok: false,
        error: data.error || `HTTP ${response.status}: ${response.statusText}`,
      };
    }

    return data as SmfFacilitatorsResponse;
  } catch (error) {
    return {
      ok: false,
      error: error instanceof Error ? error.message : 'Unknown error fetching SMF debug data',
    };
  }
}

// =============================================================================
// FORMATTERS
// =============================================================================

/**
 * Format a number as a percentage (e.g., 0.992 → "99.2%")
 */
export function formatPercent(value: number | undefined): string {
  if (value === undefined || value === null) return '-';
  return `${(value * 100).toFixed(1)}%`;
}

/**
 * Format latency in milliseconds (e.g., 142 → "142 ms")
 */
export function formatLatency(ms: number | undefined): string {
  if (ms === undefined || ms === null) return '-';
  return `${Math.round(ms)} ms`;
}

/**
 * Format invocation count (e.g., 12300 → "12.3K")
 */
export function formatInvocations(count: number | undefined): string {
  if (count === undefined || count === null) return '-';
  if (count >= 1_000_000) return `${(count / 1_000_000).toFixed(1)}M`;
  if (count >= 1_000) return `${(count / 1_000).toFixed(1)}K`;
  return count.toString();
}

/**
 * Format data age in hours (e.g., 2.1 → "2.1h ago")
 */
export function formatDataAge(hours: number | undefined): string {
  if (hours === undefined || hours === null) return '-';
  if (hours < 1) return `${Math.round(hours * 60)}m ago`;
  return `${hours.toFixed(1)}h ago`;
}

/**
 * Get confidence badge color class
 */
export function getConfidenceBadgeClass(confidence: string | undefined): string {
  switch (confidence) {
    case 'high':
      return 'bg-green-900/30 text-green-400 border-green-800';
    case 'medium':
      return 'bg-yellow-900/30 text-yellow-400 border-yellow-800';
    case 'low':
      return 'bg-orange-900/30 text-orange-400 border-orange-800';
    case 'none':
    default:
      return 'bg-gray-800 text-gray-400 border-gray-700';
  }
}

/**
 * Get row highlight class for selected facilitator
 */
export function getRowHighlightClass(selected: boolean): string {
  return selected
    ? 'bg-lime-900/20 border-lime-800'
    : 'bg-gray-900 border-gray-800';
}

